.ColorBox {
  width: 20%;
  height: 25%;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  cursor: pointer;
  margin-bottom: -3.5px;
}

.SingleColorPalette .ColorBox {
  height: 50%;
}

@media screen and (max-width: 1200px) {
  .ColorBox {
    width: 25%;
    height: 20%;
  }

  .SingleColorPalette .ColorBox {
    width: 33.3333%;
    height: 40%;
  }
}

@media screen and (max-width: 900px) {
  .ColorBox {
    width: 50%;
    height: 10%;
  }
  .SingleColorPalette .ColorBox {
    width: 50%;
    height: 20%;
  }
}

@media screen and (max-width: 600px) {
  .ColorBox {
    width: 100%;
    height: 5%;
  }

  .SingleColorPalette .ColorBox {
    width: 100%;
    height: 10%;
  }
}

.ColorBox.go-back {
  background-color: black;
}

.copy-button,
.back-button {
  width: 100px;
  height: 30px;
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -15px;
  text-align: center;
  outline: none;
  background-color: rgba(255, 255, 255, 0.3);
  font-size: 1rem;
  line-height: 30px;
  color: white;
  text-transform: uppercase;
  border: none;
  opacity: 0;
  text-decoration: none;
}

.ColorBox:hover .copy-button,
.ColorBox .back-button {
  opacity: 1;
  transition: 0.5s;
}

.box-content {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  padding: 10px;
  color: black;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 12px;
}

.see-more {
  background-color: rgba(255, 255, 255, 0.3);
  position: absolute;
  border: none;
  color: white;
  right: 0;
  bottom: 0;
  width: 60px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
}

.light-text {
  color: white;
}

.dark-text {
  color: black;
  opacity: 0.5;
}

.copy-overlay {
  opacity: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  transition: transform 0.6s ease-in-out;
  transform: scale(0.1);
}

.copy-overlay.show {
  opacity: 1;
  transform: scale(50);
  z-index: 10;
  position: absolute;
}

.copy-msg {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4rem;
  transform: scale(0.1);
  opacity: 0;
  color: white;
  flex-direction: column;
}

.copy-msg.show {
  opacity: 1;
  z-index: 10;
  transform: scale(1);
  transition: all 0.4s ease-in-out;
  transition-delay: 0.3s;
}

.copy-msg h1 {
  font-weight: 400;
  text-shadow: 1px 2px black;
  background: rgba(255, 255, 255, 0.2);
  width: 100%;
  text-align: center;
  margin-bottom: 0;
}

.copy-msg p {
  font-size: 2rem;
  font-weight: 100;
}

@media screen and (max-width: 900px) {
  .copy-msg h1 {
    font-size: 6rem;
  }

  .copy-msg p {
    font-size: 2rem;
  }
}

@media screen and (max-width: 600px) {
  .copy-msg h1 {
    font-size: 4rem;
  }
}
